import { ref } from 'vue'
import { defineStore } from 'pinia'
import type { ITraining } from '@/shared/types/training/ITraining'

export const useTrainingStore = defineStore('training', () => {
  const storeTraining = ref<ITraining[]>([])

  function storeSetTraining(i: ITraining[]) {
    storeTraining.value = i
  }

  function storeAddTraining(i: ITraining) {
    storeTraining.value.push(i)
  }

  function storeUpdateTraining(i: ITraining) {
    const index = storeTraining.value.findIndex((training) => training.id === i.id)
    storeTraining.value[index] = i
  }

  function storeRemoveTraining(i: ITraining) {
    const index = storeTraining.value.findIndex((training) => training.id === i.id)
    if (index !== -1) {
      storeTraining.value.splice(index, 1)
    }
  }

  function storeClearTraining() {
    storeTraining.value = []
  }

  return {
    storeSetTraining,
    storeTraining,
    storeAddTraining,
    storeRemoveTraining,
    storeUpdateTraining,
    storeClearTraining,
  }
})
